import { useEffect, useState } from "react";
import React from "react";
import CardWeather from "./CardWeather";
import './cardWeather.css';
import GraphPlot from "./GraphPlot";

function HourwiseData(props){
    const [hrData, setHrData] = useState([]);
    useEffect(()=>{
        getData();
    },[props.valid]);
    const getData = async () => {
        try{
            let api = `http://api.weatherapi.com/v1/forecast.json?key=4ac55578272a40fe963132616230904&q=${props.cityName}&days=1&aqi=no&alerts=no`;
            const data = await(await fetch(api)).json();
            console.log(data);
            const list = data.forecast.forecastday[0].hour;
            console.log(list);
            setHrData(list);
            props.setX([]);
            props.setY([]);
            list.map((item)=>{
                props.setX((prev)=>{
                    return [...prev,item.time.split(" ")[1]];
                });
                props.setY((prev)=>{
                    return [...prev,item.temp_c];
                })
            })
        }
        catch(e){
            throw e;
        }
    }
    
    return(
        <div className='hrData'>
        <div className='CardCollection'>
            {hrData.map((item,index)=>{
                return <CardWeather key={index} item={item}/>
            })}
        </div>
        <div className="graphplot"><GraphPlot x={props.x} y={props.y}/></div>
        </div>
    );
}

export default HourwiseData;